.contact textarea {
  font-size: 16px; 
  width: 80%; 
  min-height: 200px; 
  margin: 20px;
  line-height: 1.6;
}

.contact button {
  display: block; 
  border: 2px solid black; 
  background-color: #fff;
  padding: 10px 20px; 
  cursor: pointer;
  font-size: 20px; 
  margin: 0 20% 0 auto; 
}

.contact button:hover {
  color: yellow; 
  background-color: black;
}